@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  h1 {
    font-size: x-large;
  }
}

@layer components {
  .general-layout {
    @apply mx-auto my-3 max-w-[97%] px-[20px]
  }
}

#codeScreen {
  resize: none;
}

.logo {
  @apply w-[40px]
}

.active {
  @apply underline underline-offset-8
}

